import React, { useState } from "react";

const Component = ({ children, data, extraClass = "" }) => {
  const [activeMediaIndex, setActiveMediaIndex] = useState(0);

  const increment = () => {
    setActiveMediaIndex((i) => {
      console.log(i);
      if (i < data?.media?.length - 1) {
        return i + 1;
      } else {
        return 0;
      }
    });
  };

  return (
    <div className="product__item-wrapper mb-20">
      <div className="product__item white-bg">
        {activeMediaIndex + 1} / {data?.media?.length}
        <div className="product__thumb p-relative">
          {data?.media[activeMediaIndex]?.type === "photo" && (
            <div className="w-img" onClick={increment}>
              {data?.media?.length && (
                <img
                  src={data?.media?.length && data?.media[activeMediaIndex].url}
                  alt="product"
                />
              )}
            </div>
          )}
          <div className="w-img">
            {"" && data?.media[activeMediaIndex]?.type === "video" && (
              <video controls>
                <source
                  src={data?.media[activeMediaIndex].url}
                  type="video/mp4"
                />
                Your browser does not support the video tag.
              </video>
            )}
          </div>
        </div>
        <div className="product__content text-center">
          <h6 className="product-name">{data?.title}</h6>
          <span className="new">$720.00</span>
          <div>
            <div>
              <textarea placeholder="write your order"></textarea>
            </div>

            <i className="fal fa-minus fa-md"></i>
            {" 2 "}
            <i className="fal fa-plus fa-md"></i>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Component;
